body {
  padding: 0 0 50px;
  min-height: 100%;
  background: #F9F9FB;
}

.container-fluid {
  max-width: 1440px;
}

.container {
  max-width: 1170px;
}

.hidden {
  display: none !important;
}

.box-panel {
  margin: 0 0 24px 0;
  padding: 16px;
  background-color: #fff;
  border-radius: 4px;
  box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.15);
}

body {
  min-height: 100%;
  background: #F9F9FB;
  font-family: "Roboto", sans-serif;
  color: #4A4A4B;
  font-weight: normal;
  font-size: 14px;
}

h1, h2, h3, h4, h5, h6 {
  color: #4A4A4B;
}

h1 {
  margin: 0 0 24px 0;
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 28px;
  line-height: 33px;
}

h2 {
  margin: 0 0 20px 0;
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 28px;
}

h3 {
  margin: 0 0 16px 0;
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
}

h4 {
  margin: 0 0 12px 0;
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
}

h5 {
  margin: 0 0 8px 0;
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
}

h6 {
  margin: 0 0 8px 0;
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 10px;
  line-height: 15px;
  text-transform: uppercase;
}

p {
  margin: 0 0 16px 0;
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
}

a {
  color: #35BA59;
}

a:hover, a:focus {
  text-decoration: underline;
  color: #35BA59;
}

.__content-spaced {
  margin-bottom: 38px;
}

.error-label {
  font-weight: 500;
  color: #E21D1D;
}

.sitemap {
  margin: 0 0 20px;
}

.sitemap__item {
  margin: 0 0 14px;
}

.sitemap__link {
  font-weight: normal;
}

.color-thumb {
  padding: 16px;
  margin-bottom: 16px;
  background-color: #fff;
  box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.15);
}

.color-thumb__prev {
  margin: 0 0 8px;
  width: 48px;
  height: 48px;
  background-color: #35BA59;
  border-radius: 4px;
  float: left;
}

.color-thumb__name {
  margin-left: 56px;
  font-size: 14px;
  line-height: 1.5;
}

.color-thumb__color {
  margin-left: 56px;
  font-size: 16px;
  line-height: 1.5;
  color: #4A4A4B;
  opacity: .85;
}

.color-thumb__desc {
  clear: both;
  font-size: 14px;
}

.__color-thumb-primary .color-thumb__prev {
  background-color: #35BA59;
}

.__color-thumb-primary-light .color-thumb__prev {
  background-color: #51CE73;
}

.__color-thumb-error .color-thumb__prev {
  background-color: #E21D1D;
}

.__color-thumb-black .color-thumb__prev {
  background-color: #000;
}

.__color-thumb-grey .color-thumb__prev {
  background-color: #4A4A4B;
}

.__color-thumb-grey-light .color-thumb__prev {
  background-color: #F3F4F7;
}

.__color-thumb-white .color-thumb__prev {
  background-color: #fff;
}

.toast.__toast-showcase {
  opacity: 1;
  transform: none;
  transition: none;
}

.code-snippet {
  display: block;
  border-radius: 4px;
  background-color: #F3F4F7;
  padding: 24px;
  line-height: 24px;
  color: #4A4A4B;
}

.code-snippet .code-snippet__inj {
  color: #35BA59;
}

.toaster-container {
  position: fixed;
  left: 0;
  top: 57px;
  width: 100%;
}

.toaster-container__limiter {
  position: relative;
  max-width: 1400px;
  margin: 0 auto;
  z-index: 1;
}

.toaster {
  width: 242px;
  position: absolute;
  z-index: 10;
  right: 0;
  top: 0;
}

.toast {
  margin: 0 0 16px;
  padding: 16px 24px 24px;
  background-color: #fff;
  color: #4A4A4B;
  box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.15);
}

.toast .toast__hd {
  margin: 0 0 18px 0;
  font-weight: normal;
  font-size: 24px;
  line-height: 28px;
}

.toast .toast__body {
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
}

.toast.__success {
  color: #35BA59;
  background-color: #dff6e5;
}

.toast.__danger {
  color: #E21D1D;
  background-color: #fce8e8;
}

.toaster .toast {
  opacity: 0;
  transform: translateX(100%) scale(0.3);
  transition: all 300ms ease;
}

.toaster .toast.__show-toast {
  transform: translateX(0%) scale(1);
  opacity: 1;
}

.logo {
  display: inline-block;
  color: #4A4A4B;
  font-family: Lato;
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 29px;
}

