.checkbox {
  display: inline-block;
  width: 100%;
  margin: 0 0 16px 0;
  padding: 3px 0 0 0;
  padding-left: 32px;
  position: relative;
  min-height: 18px;
}

.checkbox__lbl {
  position: relative;
  display: inline-block;
  width: 100%;
  color: #979AA7;
  cursor: pointer;
}

.checkbox__lbl:before {
  position: absolute;
  left: -32px;
  top: 50%;
  margin-top: -9px;
  content: '';
  width: 18px;
  height: 18px;
  border: 1px solid #35BA59;
  border-radius: 4px;
}

.checkbox__lbl:after {
  position: absolute;
  content: '';
  width: 10px;
  height: 8px;
  background-image: url("data:image/svg+xml,%3Csvg width='10' height='8' viewBox='0 0 10 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M3.57143 8L0 4.15385L1 3.07692L3.57143 5.84615L9 0L10 1.07692L3.57143 8Z' fill='%2335BA59'/%3E%3C/svg%3E%0A");
  left: -28px;
  top: 50%;
  margin-top: -4px;
  opacity: 0;
}

.checkbox__input {
  min-height: 1.5em;
  position: absolute;
  z-index: 10;
  opacity: 0;
}

.checkbox .checkbox__input:checked + .checkbox__lbl::after {
  opacity: 1;
}

.radio {
  display: block;
  margin: 0 0 16px 0;
  padding: 3px 0 0 0;
  padding-left: 32px;
  position: relative;
  min-height: 18px;
}

.radio__lbl {
  position: relative;
  display: inline-block;
  width: 100%;
  color: #979AA7;
  cursor: pointer;
}

.radio__lbl:before {
  position: absolute;
  left: -32px;
  top: 50%;
  margin-top: -9px;
  content: '';
  width: 18px;
  height: 18px;
  border: 1px solid #35BA59;
  border-radius: 50%;
}

.radio__lbl:after {
  position: absolute;
  content: '';
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #35BA59;
  left: -29px;
  top: 50%;
  margin-top: -6px;
  opacity: 0;
}

.radio__input {
  min-height: 1.5em;
  position: absolute;
  z-index: 10;
  opacity: 0;
}

.radio .radio__input:checked + .radio__lbl::after {
  opacity: 1;
}

.toggle {
  display: block;
  margin: 0 0 16px 0;
  position: relative;
  min-height: 18px;
}

.toggle__lbl {
  padding: 0 0 38px 0;
  position: relative;
  display: inline-block;
  width: 100%;
  font-family: "Roboto", sans-serif;
  color: #233443;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 14px;
  cursor: pointer;
}

.toggle__lbl:before {
  position: absolute;
  left: 0;
  bottom: 0;
  content: '';
  width: 50px;
  height: 28px;
  border-radius: 14px;
  background-color: #35BA59;
  opacity: .2;
}

.toggle__lbl:after {
  position: absolute;
  content: '';
  width: 24px;
  height: 24px;
  left: 2px;
  bottom: 2px;
  border-radius: 50%;
  box-shadow: 0px 0px 6px rgba(162, 167, 168, 0.35);
  background-color: #fff;
}

.toggle__input {
  min-height: 1.5em;
  position: absolute;
  z-index: 10;
  opacity: 0;
}

.toggle .toggle__input:checked + .toggle__lbl::after {
  opacity: 1;
  left: 24px;
}

.toggle .toggle__input:checked + .toggle__lbl::before {
  opacity: 1;
}

