  border: 1px solid #35BA59;
}

.file-upload.__success .file-upload__icon {
  background: transparent url("./assets/upload-status-success.svg") center no-repeat;
}

.file-upload.__progress {
  border: 1px solid #35BA59;
}

.file-upload.__progress .file-upload__lead.__show-in-progress {
  display: block;
}

.file-upload.__progress .file-upload__lead.__show-default, .file-upload.__progress .file-upload__lead.__show-in-error {
  display: none;
}

.file-upload.__progress .file-upload__template {
  display: none;
}

.file-upload.__progress .file-upload__icon {
  background: none;
}

.file-upload.__progress .file-upload__container:before {
  content: '';
  position: absolute;
  width: 41px;
  height: 41px;
  left: 0;
  top: 50%;
  margin-top: -20px;
  animation: spinner 1s linear infinite;
  background: transparent url("./assets/upload-status-progress.svg") center no-repeat;
}

.file-upload.__error {
  border: 1px solid #E21D1D;
}

.file-upload.__error .file-upload__lead.__show-in-error {
  display: block;
}

.file-upload.__error .file-upload__lead.__show-default, .file-upload.__error .file-upload__lead.__show-in-progress {
  display: none;
}

.file-upload.__error .file-upload__icon {
  background: transparent url("./assets/upload-status-error.svg") center no-repeat;
}

.file-upload.__error .file-upload__template a {
  color: #E21D1D;
}

#progress-bar {
  display: none;
}

